import { Card, Grid, styled, useTheme, Icon, IconButton, Box } from '@mui/material';
import { Fragment, useEffect, useState } from 'react';
import Campaigns from './shared/Campaigns';
import DoughnutChart from './shared/Doughnut';
import RowCards from './shared/RowCards';
import StatCards from './shared/StatCards';
import StatCards2 from './shared/StatCards2';
import TopSellingTable from './shared/TopSellingTable';
import UpgradeCard from './shared/UpgradeCard';
import KeyValueCards from 'app/views/dashboard/shared/KeyValueCards';
import { useSelector } from 'react-redux';
import Parse from 'app/vendor/Parse';
import { ca } from 'date-fns/locale';
import NumberConsole from 'app/components/NumberConsole'
import ItemContainer from 'app/components/ItemContainer'
import ValueComponent from 'app/components/ValueComponent'


const tagItem = async (id, key, value) => {
  return await Parse.Cloud.run('tagItem', {
    id,
    key,
    value
  })
}

const comparator = (a, b, key) => {
  const item1 = a[key];
  const item2 = b[key]
  if (item1 < item2) {

    return -1;
  }
  if (item1 > item2)
    return 1;
  return 0;
}

const renderData = (row, master) => {
  const { value } = row
  const numberResult = value?.reduce((prev, { query, result }) => {
    const numbers = result.map(e => e.num)
    return prev.concat(numbers)
  }, [])
  const queryResult = value?.reduce((prev, { query, result }) => {
    return prev.concat([query])
  }, [])
  const distinctQuery = [...new Set(queryResult)]
  const distinctNumberResult = [...new Set(numberResult)]
  return <>
    <Box>
      {
        value?.map(e => <Box>
          {e.query} {e.date.substr(0, 10)}
        </Box>
        )
      }

    </Box>
    <Box display={'flex'} flexDirection={"row"}>

      <Box>
        {
          <h2> {distinctQuery?.length} Query</h2>
        }
        {
          distinctQuery?.map((query) => {
            return <Box> {query}</Box>;
          })
        }
      </Box>

      <Box>
        {
          <h2> {distinctNumberResult.length} result</h2>
        }
        {
          distinctNumberResult
            .map(e => {
              const item = master.find(ee => ee.num === e)

              return {
                ...item,
                num: e,
              }
            })
            .sort((a, b) => comparator(a, b, "sourceId"))
            .map((query) => {


              return <Box> {query.num}
                {
                  query.sourceId
                }
              </Box>;
            })
        }
      </Box>


    </Box>
    {
      //JSON.stringify(row)
    }
  </>
}



const Component = ({ itemKey }) => {

  const [masterList, setMasterList] = useState([])

  useEffect(async () => {
    const res = await Parse.Cloud.run("searchResellerNumbers", {
      query: ''
    })

    setMasterList(res)
  }, [])
  const columns = [
    {
      header: 'Search',
      render: row => <ValueComponent
        key={row.objectId}
        value={!!row.search}
        onChange={(v) => tagItem(row.objectId, 'search', v)}
      />
    },
  ]

  return (
    <Fragment>
      <ItemContainer
        itemKey={itemKey}
        itemColumns={columns}
        renderData={(row) => renderData(row, masterList)}
      >
      </ItemContainer>
    </Fragment>
  );
};

export default Component;
